import React, { ReactElement, useCallback, useState } from 'react';
import { selectInterestResultState } from '../../store/interestCalculatorStore';
import { useAppSelector } from '../../../../app/hooks/hooks';
import styles from './InterestResult.module.sass';
import { IconTip } from '../../../../common/components/IconTip/IconTip';

export const InterestResult = (): ReactElement => {
  const resultState = useAppSelector(selectInterestResultState);
  const [showPermalink, setShowPermalink] = useState(false)

  const togglePermalinkDisplay = useCallback(
    () => setShowPermalink(!showPermalink),
    [showPermalink],
  );

  return (
    <div className={styles.calculationResult}>
      {resultState.calculationStatus === 'pending' &&
        <div className={styles.calculationResultLoader}>Loading...</div>
      }
      <h4><span className="me-2">🧮</span>Calculation</h4>
      <div className='mb-3'>
        <div className={styles.calculationResultShowPermalink} onClick={togglePermalinkDisplay}>
          Show permalink
        </div>
        {showPermalink &&
          <code className={styles.calculationResultLink}>
            {resultState.calculationLink}
          </code>
        }
      </div>
      <div className={styles.calculationResultRow}>
        <label htmlFor="finalCapitalValue">
          Final capital
          <span className="ms-2">
            <IconTip
              tipContent="This is how much capital you will have at the end. The final capital is made up of the total deposits and the interest received"
            />
          </span>
        </label>
        <span id='finalCapitalValue'>{resultState.finalCapital}€</span>
      </div>

      <div className={styles.calculationResultRow}>
        <label htmlFor="totalPayments">
          Total deposits
          <span className="ms-2">
            <IconTip
              tipContent="The sum of all your deposits / savings installments (excluding interest)"
            />
          </span>
        </label>
        <span id='totalPayments'>{resultState.totalPayments}€</span>
      </div>

      <div className={styles.calculationResultRow}>
        <label htmlFor="totalInterest">
          Total Interest payments received
          <span className="ms-2">
            <IconTip
              tipContent="All interest (and compound interest) you received during the investment period"
            />
          </span>
        </label>
        <span id='totalInterest'>{resultState.totalInterest}€</span>
      </div>
      <div>
        If you invest <b>€{resultState.formData.monthlySavingsRate} per month at {resultState.formData.annualInterestRate}% over {resultState.formData.savingsPeriod} years</b>,
        you will end up with a <b>final capital of €{resultState.finalCapital}</b>. This is made up of <b>€{resultState.totalPayments} in deposits</b> and <b>€{resultState.totalInterest} in interest</b> or capital gains.
      </div>

    </div>
  );
}