import React, { ReactElement } from 'react';
import { InterestForm } from './InterestForm/InterestForm';
import { InterestResult } from './InterestResult/InterestResult';
import { useAppSelector } from '../../../app/hooks/hooks';
import { selectInterestResultState } from '../store/interestCalculatorStore';

export const InterestCalculator = (): ReactElement => {
  const calculationResultState = useAppSelector(selectInterestResultState);
  return (
    <div>
      <InterestForm />
      {calculationResultState.calculationStatus !== 'initial' &&
        < InterestResult />
      }
    </div>
  );
}