import { PayloadAction, createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import { RootState } from '../../../app/store/store';

export enum DistributionInterval {
  monthly = 'monthly',
  quarterly = 'quarterly',
  yearly = 'yearly',
}

interface CalulationApiResult {
  finalCapital: string;
  totalInterest: string;
  totalPayments: string;
}

export interface InterestCaclulatorFromState {
  initialCapital: string;
  monthlySavingsRate: string;
  savingsPeriod: number;
  annualInterestRate: number;
  distributionInterval: DistributionInterval;
}

export interface InterestCalculatorResultState extends CalulationApiResult {
  calculationStatus: 'successful' | 'failed' | 'pending' | 'initial';
  calculationLink: string;
  formData: InterestCaclulatorFromState;
}

export interface InterestCaclulatorState {
  calculationForm: InterestCaclulatorFromState;
  resultState: InterestCalculatorResultState;
}

const initialFormData: InterestCaclulatorFromState = {
  initialCapital: '5.000',
  monthlySavingsRate: '100',
  savingsPeriod: 10,
  annualInterestRate: 5,
  distributionInterval: DistributionInterval.monthly,
};

const initialState: InterestCaclulatorState = {
  calculationForm: initialFormData,
  resultState: {
    calculationStatus: 'initial',
    calculationLink: '',
    finalCapital: '',
    totalInterest: '',
    totalPayments: '',
    formData: initialFormData,
  },
};

export const calculateResult = createAsyncThunk<CalulationApiResult, string>(
  'interestCalculator/calculateResult',
  async (calculationLink) => {
    const response = await fetch(calculationLink);
    const result: CalulationApiResult = await response.json();

    return result;
  }
);

export const interestCalculatorStore = createSlice({
  name: 'interestCalculator',
  initialState,
  reducers: {
    changeField: (state, action: PayloadAction<Partial<InterestCaclulatorFromState>>) => {
      state.calculationForm = {
        ...state.calculationForm,
        ...action.payload
      };
    },
    rememberFormData: (state, { payload }: PayloadAction<InterestCaclulatorFromState & {calculationLink: string}>) => {
      state.resultState.formData = payload;
      state.resultState.calculationLink = payload.calculationLink;
    },
  },
  extraReducers: (builder) => {
    builder
      .addCase(calculateResult.pending, (state) => {
        state.resultState.calculationStatus = 'pending';
      })
      .addCase(calculateResult.fulfilled, (state, action) => {
        state.resultState.calculationStatus = 'successful';
        state.resultState.finalCapital = action.payload.finalCapital.replace('€', '');
        state.resultState.totalInterest = action.payload.totalInterest.replace('€', '');
        state.resultState.totalPayments = action.payload.totalPayments.replace('€', '');
      })
      .addCase(calculateResult.rejected, (state) => {
        state.resultState.calculationStatus = 'failed';
      });
  },
});

export const { changeField, rememberFormData } = interestCalculatorStore.actions;

export const selectInterestFormState = (state: RootState) => state.interestCalculator.calculationForm;
export const selectInterestResultState = (state: RootState) => state.interestCalculator.resultState;

export const { reducer: interestCalculatorReducer  } = interestCalculatorStore;