export const addPointsToNumberValue = (numberValue: string): string => {
  let formated = '';
  const numberValueWithoutDotsReverted = revertString(numberValue.replaceAll('.', ''));
  for (var i = 0; i < numberValueWithoutDotsReverted.length; i++) {
    if (i !== 0 && i % 3 === 0) {
      formated += '.';
    }
    formated += numberValueWithoutDotsReverted[i];
  }

  return revertString(formated);
}

export const revertString = (string: string): string => {
  return string.split("").reverse().join("");
}