import React, { ReactElement, useCallback } from 'react';
import { Button, Form, InputGroup } from 'react-bootstrap';
import { IconTip } from '../../../../common/components/IconTip/IconTip';
import styles from './InterestForm.module.sass';
import { DistributionInterval, calculateResult, changeField, rememberFormData, selectInterestFormState } from '../../store/interestCalculatorStore';
import { useAppDispatch, useAppSelector } from '../../../../app/hooks/hooks';
import { addPointsToNumberValue } from '../../../../helpers/helpers';

export const InterestForm = (): ReactElement => {
  const formState = useAppSelector(selectInterestFormState);
  const dispatch = useAppDispatch();

  const submitHandler = useCallback(() => {
    const calculationUrl = new URL('https://api.finanzfluss.de/calculator/compound-interest-calculator');
    calculationUrl.searchParams.set('startCapital', formState.initialCapital.replaceAll('.', ''));
    calculationUrl.searchParams.set('durationYears', formState.savingsPeriod.toString());
    calculationUrl.searchParams.set('monthlyPayment', formState.monthlySavingsRate.replaceAll('.', ''));
    calculationUrl.searchParams.set('yearlyInterest', formState.annualInterestRate.toString());
    calculationUrl.searchParams.set('type', formState.distributionInterval.toString());

    dispatch(calculateResult(calculationUrl.toString()));
    dispatch(rememberFormData({
      ...formState,
      calculationLink: calculationUrl.toString(),
    }));
  }, [formState]);

  return (
    <div className={styles.interestForm}>
      <h4 className="mb-4"><span className="me-1">💰</span> Compound interest calculator</h4>

      <Form>
        <Form.Label htmlFor="initialCapitalInput">
          Initial Capital
          <span className="ms-2">
            <IconTip
              tipContent="Your starting capital, which you want to invest right from the beginning (can also be €O if you only want to save monthly)"
            />
          </span>
        </Form.Label>
        <InputGroup className="mb-3">
          <Form.Control
            type="text"
            placeholder="5000"
            id="initialCapitalInput"
            value={formState.initialCapital}
            onChange={(e) => dispatch(
              changeField({
                initialCapital: addPointsToNumberValue(e.target.value)
              })
            )}
          />
          <InputGroup.Text className="input-group-text">€</InputGroup.Text>
        </InputGroup >

        <Form.Label htmlFor="monthlySavingsRate">
          Monthly savings rate
          <span className="ms-2">
            <IconTip
              tipContent="This is how much you want to invest monthly (it can also be €0 if you only want to invest once at the beginning)"
            />
          </span>
        </Form.Label>
        <InputGroup className="mb-3">
          <Form.Control
            type="text"
            placeholder="100"
            id="monthlySavingsRate"
            value={formState.monthlySavingsRate}
            onChange={(e) => dispatch(
              changeField({
                monthlySavingsRate: addPointsToNumberValue(e.target.value)
              })
            )}
          />
          <InputGroup.Text>€</InputGroup.Text>
        </InputGroup >

        <Form.Label htmlFor="savingsPeriod">
          Savings period / Investment period
          <span className="ms-2">
            <IconTip
              tipContent="Here you enter your investment period in years. If you have a monthly savings rate, this is also your savings period"
            />
          </span>
        </Form.Label>
        <InputGroup className="mb-3">
          <Form.Control
            type="number"
            placeholder="10"
            id="savingsPeriod"
            value={formState.savingsPeriod}
            onChange={(e) => dispatch(
              changeField({
                savingsPeriod: parseInt(e.target.value)
              })
            )}
          />
          <InputGroup.Text>Years</InputGroup.Text>
        </InputGroup >

        <Form.Label htmlFor="annualInterestRate">
          Annual interest rate
          <span className="ms-2">
            <IconTip
              tipContent="The interest rate you receive year after year over your investment period"
            />
          </span>
        </Form.Label>
        <InputGroup className="mb-3">
          <Form.Control
            type="number"
            placeholder="5"
            id="annualInterestRate"
            value={formState.annualInterestRate}
            onChange={(e) => dispatch(
              changeField({
                annualInterestRate: parseInt(e.target.value)
              })
            )}
          />
          <InputGroup.Text>%</InputGroup.Text>
        </InputGroup >

        <Form.Label htmlFor="distributionInterval">
          Distribution interval
          <span className="ms-2">
            <IconTip
              tipContent="At what interval should the interest be added?"
            />
          </span>
        </Form.Label>
        <Form.Select
          id="distributionInterval"
          value={formState.distributionInterval}
          onChange={(e) => dispatch(
            changeField({
              distributionInterval: e.target.value as DistributionInterval
            })
          )}
        >
          <option value="monthly">monthly</option>
          <option value="quarterly">quarterly</option>
          <option value="yearly">yearly</option>
        </Form.Select>

        <div className="d-grid">
          <Button className="mt-5" onClick={submitHandler}>Calculate</Button>
        </div>
      </Form>
    </div>
  );
}
