import React, { ReactElement } from 'react';
import styles from './IconTip.module.sass';
import { OverlayTrigger, Tooltip } from 'react-bootstrap';

interface HelpIconProps {
  tipContent: string;
}

export const IconTip = ({ tipContent }: HelpIconProps): ReactElement => {
  return (
    <OverlayTrigger
      placement="top"
      overlay={ <Tooltip>{ tipContent }</Tooltip> }>
        <span className={styles.helpIcon}>?</span>
    </OverlayTrigger>
  )
}